import Data from "@/js/core/data";

const Test_Info_Server = "https://api.${region}.500apps.com/ninjainterview/question/";
const Candidate_Save_Server = "https://api.${region}.500apps.com/ninjainterview";
const Test_Save_Server = "https://api.${region}.500apps.com/ninjainterview/";
const Candidate_test_Save_Server = "https://api.${region}.500apps.com/ninjainterview";

class PositionTest extends Data {
  constructor() {
    super(Test_Info_Server);
    this.disablePCors();
  }
  // Get test info and questions
  getTest(domain_id, test_id) {
    this.updateServer(Test_Info_Server);
    this.url = `${this.url}${domain_id}/${test_id}`;
    return this.get();
  }
  //Save candidate information
  async addCandidate(domain_id, position_id, form_data) {
    this.updateServer(Candidate_Save_Server);
    this.url = `${this.url}/${domain_id}/${position_id}/candidate`;
    return await this.multi_call(form_data);
  }

  //Save candidate test information
  async addCandidateTest(domain_id, test_id, candidate_id) {
    this.updateServer(Candidate_test_Save_Server);
    this.url = `${this.url}/${domain_id}/${test_id}/${candidate_id}/candidate/test`;
    return await this.get();
  }

  // Update server
  updateServer(server_url) {
    // eslint-disable-next-line no-unused-vars
    let region = window.region;
    this.url = eval("`" + server_url + "`");
  }
  async saveResults(form_data, test_duration, domain_id) {
    this.updateServer(Test_Save_Server);
    this.url = `${this.url}${domain_id}/test/response`;
    form_data = { test_response: form_data, test_duration: test_duration };
    return await this.multi_call(form_data);
  }
}
export default new PositionTest();
